import React, { Component } from 'react';

const IMAGES = ['/public/movies/harry_potter.jpg'];

class Hero extends Component {
	state = {
		img: IMAGES[0],
	};
	renderImage() {
		return (
			<>
				<img src={this.state.img} />
			</>
		);
	}

	render() {
		return <>{this.renderImage()}</>;
	}
}

export default Hero;
